
.label.is-moving {
  -webkit-animation: beat  5s linear infinite;
  animation: beat 5s linear infinite;
}

@keyframes beat {
  0% {
    opacity: 0.7;
  }
  50% {
    opacity: 1;
  }
  100% {
    opacity: 0.7;
  }
}

@-webkit-keyframes beat {
  0% {
    opacity: 0.7;
  }
  50% {
    opacity: 1;
  }
  100% {
    opacity: 0.7;
  }
}




