
.temperature-menu{
  margin-bottom:15px;
  border-radius:5px;
  padding:5px;
  background:rgba(0,0,0,.7);
  color:white;
  width:150px;
  overflow:hidden;
  font-size:2.5em;
  -webkit-transition:width 0.5s;
  transition: width 0.5s;
}
.temperature-menu.is-open{
  width:300px;
}
.friendly-name{
  float:left;
  width:calc(100%);
  margin-right:50%;
}
.temperature-wrap{
  width:300px;
}
.current-temp{
  cursor:pointer;
  width:50%;
}
.target-temp{
  width:50%;
}
.button.temp-select{
  margin-top: 10px;
  opacity:.5;
}
.button.temp-select.is-selected{
  opacity:1;
}

.label.is-on {
  background: #c60f13;
}

.label.is-heating {
  -webkit-animation: beat  5s linear infinite;
  animation: beat 5s linear infinite;
}

@keyframes beat {
  0% {
    opacity: 0.7;
  }
  50% {
    opacity: 1;
  }
  100% {
    opacity: 0.7;
  }
}

@-webkit-keyframes beat {
  0% {
    opacity: 0.7;
  }
  50% {
    opacity: 1;
  }
  100% {
    opacity: 0.7;
  }
}




